// GET å–å¾—ç”¢å“åˆ—è¡¨ (v)
function getProductsList(){
  axios.get(`https://livejs-api.hexschool.io/api/livejs/v1/customer/${apiPath}/products`)
  .then(function (response) {
    // æˆåŠŸæœƒå›å‚³çš„å…§å®¹
    //console.log(response);
    productsData = response.data.products;
    renderProductsList();
  });
};

//select ç¯©é¸ç”¢å“åˆ—è¡¨ 
select.addEventListener('change',function(e){
  let category = e.target.value;
  if( category == 'å…¨éƒ¨'){
    renderProductsList();
    return;
  }else{
    //ğŸ¤”
    let str = '';
    productsData.forEach(function(item){
      if( category == item.category){
        str += `<li class="productCard">
          <h6 class="productType">NEW</h6>
          <img src="${item.images}" alt="${item.title}">
          <a class="js-addCart" data-id="${item.id}">åŠ å…¥è³¼ç‰©è»Š</a>
          <h5>${item.title}</h5>
          <del class="originPrice h6">NT$${item.origin_price}</del>
          <h3 class="nowPrice">NT$${item.price}</h3>
      </li>`;}
    });
    productList.innerHTML= str;
  }
})

//renderç”¢å“åˆ—è¡¨ 
function renderProductsList(){
  let str ='';
  productsData.forEach(function(item){
      str += `<li class="productCard">
          <h6 class="productType">NEW</h6>
          <img src="${item.images}" alt="${item.title}">
          <a class="js-addCart" data-id="${item.id}">åŠ å…¥è³¼ç‰©è»Š</a>
          <h5>${item.title}</h5>
          <del class="originPrice h6">NT$${item.origin_price}</del>
          <h3 class="nowPrice">NT$${item.price}</h3>
      </li>`;
    });
  productList.innerHTML= str;
}

