// POST åŠ å…¥è³¼ç‰©è»Š(v)
productList.addEventListener('click',function(e){
  e.preventDefault(); //æ¶ˆé™¤é è¨­è¡Œç‚º
  let addCartClass = e.target.getAttribute('class');
  console.log(addCartClass);
  console.log(addCartClass !== "js-addCart")
  if( addCartClass !== "js-addCart" ){
    return;
  }
  let ProductId = e.target.getAttribute('data-id');
  addItemToCart(ProductId);
  
});
function addItemToCart(ProductId){
  let ProductNum = 1;
  cartData.forEach(function(item){
    if( ProductId == item.product.id){
      ProductNum = item.quantity += 1 ;
    }
  })
  axios.post(`https://livejs-api.hexschool.io/api/livejs/v1/customer/${apiPath}/carts`, {
    data: {
    "productId": ProductId,
    "quantity": ProductNum
    }
  })
  .then(function (response) {
    alert('åŠ å…¥è³¼ç‰©è»ŠæˆåŠŸ');
    init();
    getCartsList();
    console.log(response.data);
  });
};

